html,
body {
  width: 100%;
  height: 100%;
  color: white;
  text-align: center;
  font-family: helvetica, arial;
}

header,
nav,
main,
footer,
article {
  -webkit-flex-basis: auto;
          flex-basis: auto;
  margin: 0.4em;
  padding: 1em;
  border-radius: 6px;
  text-align: center;
}

article {
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 100%;
}

header {
  background: deeppink;
  padding: 1rem;
}

main {
  background: grey;
  color: #444;
  padding: 1rem;
  width: 60%;
  overflow: auto; /* Empêche le dépassement des flottants. */
}

nav {
  background: whitesmoke;
  color: #444;
  padding: 1rem;
  width: 40%;
  overflow: auto; /* Empêche le dépassement des flottants. */
}

footer {
  background: purple;
  padding: 1rem;
}

b {
  color: purple;
  margin-left: 20px;
  float: left;
}

.content {
  display: -webkit-flex;
  display: flex;
}

.contenerInfoSensor {
  color: #444;
  padding: 1rem;
  width: 60%;
  float: left;
  overflow: auto; /* Empêche le dépassement des flottants. */
}

.contenerHistoricTable {
  color: #444;
  padding: 1rem;
  width: 40%;
  float: right;
  overflow: auto; /* Empêche le dépassement des flottants. */
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

